html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
  background-color: #f4f4f4;
}

#root {
  height: 100%;
  width: 100%;
  position: relative;
}

/* Absolutely centered app container */
.App {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 600px;
  height: 850px;
  transform: translate(-50%, -50%);
  overflow: hidden;
  background-color: white;
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.2);
}

/* Flipbook styling */
.flipbook {
  width: 100%;
  height: 100%;
}

/* Page container */
.page {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

/* Image styling */
.flip-img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  display: block;
  user-select: none;
  pointer-events: none;
  background-color: white;
}

/* Navigation buttons */
.nav-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1000;
  background: rgba(255, 255, 255, 0.8);
  border: none;
  font-size: 2rem;
  padding: 10px 15px;
  color: black;
  cursor: pointer;
  border-radius: 50%;
}

.prev {
  left: 20px;
}

.next {
  right: 20px;
}
